  .toast{
    position: absolute;
    top: 25px;
    right: 30px;
    border-radius: 12px;
    background: #fff;
    padding: 20px 35px 20px 25px;
    box-shadow: 0 5px 10px rgba(0,0,0,0.1);
    border-left: 6px solid #4070f4;
    overflow: hidden;
    transform: translateX(calc(100% + 30px));
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.35);
}
.toast.active{
    transform: translateX(0%);
}
.toast .toast-content{
    display: flex;
    align-items: center;
}
.toast-content .check{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    width: 35px;
    background-color: #4070f4;
    color: #fff;
    font-size: 20px;
    border-radius: 50%;
}
.toast-content .message{
    display: flex;
    flex-direction: column;
    margin: 0 20px;
}
.message .text{
    font-size: 20px;
    font-weight: 400;;
    color: #666666;
}
.message .text.text-1{
    font-weight: 600;
    color: #333;
}
.toast .close{
    position: absolute;
    top: 10px;
    right: 15px;
    padding: 5px;
    cursor: pointer;
    opacity: 0.7;
}
.toast .close:hover{
    opacity: 1;
}
.toast .progress{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    width: 100%;
    background: #ddd;
}
.toast .progress:before{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: #4070f4;
}
.progress.active:before{
    animation: progress 5s linear forwards;
}
@keyframes progress {
    100%{
        right: 100%;
    }
}
.toast.active ~ button{
    pointer-events: none;
}
  @media screen and (min-width: 1000px){
    .split-screen{
        flex-direction: row;
        height: 100vh;
    }
    .left , .right{
        display: flex;
        width: 50%;
        height: auto;
    }
    .left .copy p{
        display: block;
    }
    .left{
        flex-basis: 70%;
    }
    .split-screen::-webkit-scrollbar {
        display: none;
    }
    .right{
        -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
    overflow-y: scroll;
    }
    
}

@media screen and (max-width: 999px){
    .left h2{
        margin: auto;
        margin-left: 2px;
        
    }
    .copy .loginword, .copy .createword{
        margin-top: 3.2rem;
    }
    .left{
        width: 101%;
        height: 9rem;
    }
}